.wiki-main {
  margin-top: 100px;
  width: 100%;
  height: calc(100vh - 100px);
  margin-top: 100px;
  box-sizing: border-box;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 20px;
  @include resp {
     height: calc(100vh - 70px);
     margin-top: 70px; }

  &_archive {
    .wiki-top__btn {
      background-color: $accent;
      border-color: $accent;
      &:hover {
        background-color: #fff;
        border-color: #000;
        color: #000; } }
    .wiki__btn {
      background-color: rgba(0,0,0,.8);
      border-color: rgba(0,0,0,.8);
      color: #fff;
      &:hover {
        background-color: $accent;
        border-color: $accent;
        color: #000; } } } }

.wiki-top {
  height: 130px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include rmin(768) {
    height: 60px; }
  @include rmin(1366) {
    height: 95px; }
  @include rmin(1600) {
    height: 130px; }
  &__title {
    font-size: 30px;
    color: #585858;
    text-transform: uppercase;
    @include futura-bold;
    @include rmin(768) {
      font-size: 18px; }
    @include rmin(1366) {
      font-size: 30px; } }
  &__btn {
    margin-right: 7%;
    transition: .3s;
    &:hover {
      background-color: #000;
      color: #fff; } }
  .controll-btn_right {
    margin-left: 10px; } }

.wiki-content-wrap {
  height: calc(100% - 130px);
  @include rmin(768) {
    height: calc(100% - 60px); }
  @include rmin(1366) {
    height: calc(100% - 95px); }
  @include rmin(1600) {
    height: calc(100% - 130px); }
  .slick-track,
  .slick-list {
    height: 100%; } }

.wiki-content {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-content: space-between;
  flex-flow: column wrap; }

.wiki {
  background-size: cover;
  background-position: 50% 30%;
  box-sizing: border-box;
  padding: 50px 35px;
  position: relative;
  @include rmin(768) {
    padding: 15px 10px; }
  @include rmin(1366) {
    padding: 50px 35px; }
  &.grid_2-3 .wiki__title {
    @include rmin(1366) {
      font-size: 10px; }
    @include rmin(1600) {
      font-size: 12px; } }
  &.grid_1-2 {
    @include rmin(768) {
      padding: 15px 10px; }
    @include rmin(1366) {
      padding: 20px 15px; }
    .wiki__title {
      @include rmin(1366) {
        font-size: 9px; }
      @include rmin(1600) {
        font-size: 12px; } } }
  &.grid_1-3 {
    @include rmin(768) {
      padding: 15px 10px; }
    @include rmin(1366) {
      padding: 20px 15px; }
    .wiki__title {
      @include rmin(768) {
        font-size: 4px; }
      @include rmin(1366) {
        font-size: 8px; }
      @include rmin(1600) {
        font-size: 11px; } } }
  &.grid_1-1 {
    @include rmin(768) {
      padding: 15px 10px; }
    @include rmin(1366) {
      padding: 20px 15px; }
    .wiki__title {
      @include rmin(768) {
        font-size: 4px; }
      @include rmin(1366) {
        font-size: 6px; }
      @include rmin(1600) {
        font-size: 8px; } } }

  &_comming-soon {
    background-image: url(../img/wiki-main/wiki-main-comming-soon.jpg);
    background-size: cover;
    background-position: 50% 50%;
    font-size: 24px;
    color: $accent;
    text-transform: uppercase;
    text-align: center;
    @include rmin(768) {
      font-size: 14px; }
    @include rmin(1366) {
      font-size: 18px; }
    @include rmin(1600) {
      font-size: 24px; }
    span {
      padding-top: 30%;
      padding-left: 5px;
      padding-right: 5px;
      display: block; } }

  &__title {
    width: 75%;
    color: #fff;
    @include futura-bold;
    text-transform: uppercase;
    font-size: 18px;
    span {
      display: block; }
    @include rmin(768) {
      font-size: 7px; }
    @include rmin(1366) {
      font-size: 12px; }
    @include rmin(1600) {
      font-size: 18px; }
    &_name {
      font-size: 6.6666em;
      line-height: .7em; }
    &_date {
      display: block;
      font-size: 3.8888em; }
    &_year {
      display: block;
      font-size: 6.6666em;
      line-height: .5em; } }

  &__btn {
    border-color: $accent;
    text-transform: uppercase;
    background-color: $accent;
    display: block;
    position: absolute;
    right: 7%;
    transition: .3s;
    @include casp-bold;
    &:hover {
      border-color: #000;
      background-color: #fff; }
    @include rmin(768) {
      min-width: 160px;
      font-size: 14px;
      bottom: 10px; }
    @include rmin(1366) {
      min-width: 190px;
      font-size: 16px;
      bottom: 40px; }
    @include rmin(1600) {
      min-width: 230px;
      font-size: 16px; } } }

.grid_1-1 {
  width: calc(25% - 5px);
  height: calc(50% - 5px); }

.grid_1-2 {
  width: calc(50% - 5px);
  height: calc(50% - 5px); }

.grid_1-3 {
  width: calc(33.3333% - 5px);
  height: calc(50% - 5px); }

.grid_2-2 {
  width: calc(50% - 5px);
  height: 100%; }

.grid_2-3 {
  width: calc(33.3333% - 5px);
  height: 100%; }

.grid_2-4 {
  width: 100%;
  height: 100%; }
