.more {
	&__img {
		height: 360px;
		max-height: 30vh;
		background: #ccc center center / cover no-repeat; }
	&__title {
		margin-bottom: 20px;
		@include futura-bold;
		font-size: 30px;
		text-transform: uppercase;
		@include resp {
			margin-bottom: 15px;
			font-size: 24px; } }
	&__body {
		padding: 33px 40px 23px;
		@include resp {
			max-height: calc(70vh - 200px);
			padding: 17px;
			overflow: auto; } }
	&__text {
		font-size: 18px;
		color: #6c6c6c;
		margin-bottom: 35px;
		@include resp {
			font-size: 16px;
			margin-bottom: 0; } }
	&__btns {
		@include resp {
			position: absolute;
			bottom: 0;
			left: 20px;
			right: 20px; } }
	.btn {
		width: 230px;
		display: block;
		margin-bottom: 20px;
		&_large {
			width: 275px; }
		@include r(1366) {
			width: 180px;
			&_large {
				width: 215px; } }
		@include r(960) {
			height: 30px;
			width: 140px;
			line-height: 30px;
			font-size: 14px;
			text-transform: uppercase;
			&_large {
				font-size: 12px;
				width: 190px; } } } }
