.wiki-extended {
  position: relative;
  overflow: hidden;
  height: calc(100vh - 100px);
  margin-top: 100px;
  @include resp {
     height: calc(100vh - 70px);
     margin-top: 70px; }

  &__view {
     position: relative;
     float: left;
     width: 50%;
     height: 100%;
     transition: background-color .5s;

     .close {
       display: none;
       width: 70px;
       height: 70px;
       background-color: rgba(0,0,0,.7);
       right: 0;
       top: 50%;
       transform: translateY(-50%); }
     &.opened {
       .close {
         display: block; } } }

  &__descr {
    width: 50%;
    height: 100%;
    float: right;
    position: relative;
    box-sizing: border-box;
    padding: 5% 3%; }

  &__date {
    @include futura-demi;
    color: #676767;
    @include rmin(768) {
      font-size: 14px;
      margin-bottom: 5px; }
    @include rmin(1366) {
      font-size: 18px;
      margin-bottom: 25px; } }
  &__title {
    @include futura-demi;
    @include rmin(768) {
      font-size: 21px;
      margin-bottom: 5px; }
    @include rmin(1366) {
      font-size: 36px;
      margin-bottom: 25px; } }
  &__text {
    @include futura-demi;
    @include rmin(768) {
      font-size: 14px;
      line-height: 16px; }
    @include rmin(1366) {
      font-size: 18px;
      line-height: 24px; } }
  &__btn-container {
    position: absolute;
    left: 6%;
    bottom: 40px;
    width: 100%; }

  &__btn {
    display: inline-block;
    text-transform: uppercase;
    @include rmin(768) {
      min-width: 160px; }
    @include rmin(1366) {
      min-width: 190px; }
    @include rmin(1600) {
      min-width: 230px; }

    &_main {
      margin-right: 30px;
      background-color: $accent;
      border-color: $accent;
      &:hover {
        background-color: #fff;
        border-color: #000; } }
    &_calendar {
      &:hover {
        background-color: $accent;
        border-color: $accent; } } } }

.wiki-extended-slider {
  position: relative;
  z-index: 3;
  height: 100%;
  &__video {
    display: none;
    width: 100%;
    height: 100%;
    background-color: #161616;
    position: relative; }
  &__video-btn {
    width: 270px;
    position: absolute;
    text-align: center;
    cursor: pointer;
    left: 50%;
    transform: translateX(-50%);
    bottom: 35%;
    span {
      font-size: 30px;
      @include casp-bold;
      color: #fff;
      display: block;
      padding-top: 100px;
      text-shadow: 0 0 16px rgba(0,0,0,.7);
      @include rmin(768) {
        font-size: 21px;
        padding-top: 55px; }
      @include rmin(1366) {
        font-size: 24px;
        padding-top: 75px; }
      @include rmin(1600) {
        font-size: 30px;
        padding-top: 100px; }
      &:after {
        content: '';
        display: block;
        position: absolute;
        width: 95px;
        height: 95px;
        background-image: url(../img/video-btn.png);
        @include cover;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        @include rmin(768) {
          width: 50px;
          height: 50px; }
        @include rmin(1366) {
          width: 70px;
          height: 70px; }
        @include rmin(1600) {
          width: 95px;
          height: 95px; } } } }
  &__body {
    height: 100%;
    position: relative;
    .slick-slider,
    .slick-list,
    .slick-track {
      height: 100%; }
    .slick-next,
    .slick-prev {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      display: inline-block;
      width: 30px;
      height: 57px;
      cursor: pointer;
      z-index: 999;
      border: none;
      background-color: transparent;
      font-size: 0;
      opacity: .3;
      transition: opacity .3s;
      &:hover {
        opacity: 1; } }
    .slick-prev {
      left: 20px;
      background-image: url(../img/archive-extend/ar-l.png); }
    .slick-next {
      right: 20px;
      background-image: url(../img/archive-extend/ar-r.png); }
    .slick-track > div {
       z-index: 4;
       width: 100%;
       height: 100%;
       cursor: pointer;
       @include cover; } }
  &__controll {
    width: 100%;
    height: 100%;
    position: relative; }
  //&__title
  //position: absolute
  //left: 50px
  //top: 100px
  //@include futura-bold
  //span
  //  display: block
  //@include rmin(768)
  //  font-size: 7px
  //@include rmin(1366)
  //  font-size: 12px
  //@include rmin(1600)
  //  font-size: 18px
  &__nav {
     position: absolute;
     left: 50%;
     text-align: center;
     transform: translateX(-50%);
     height: 100px;
     z-index: 5;
     @include rmin(768) {
       bottom: 0;
       width: 360px; }
     @include rmin(1366) {
       bottom: 30px;
       width: 660px; }
     .slick-slide {
        display: inline-block;
        vertical-align: top; }
     .slick-track {
       margin: 0 auto;
       div {
          position: relative;
          width: 99px !important;
          height: 100px;
          background: no-repeat center center;
          background-size: cover;
          cursor: pointer;
          z-index: 6;
          opacity: .3;
          transition: .3s;
          &:not(:last-child) {
            margin-right: 10px; }
          @include rmin(768) {
            width: 50px !important;
            height: 50px; }
          @include rmin(1366) {
            width: 100px !important;
            height: 100px; }
          &:hover {
             opacity: 1; }
          &.slick-current {
            opacity: 1; } } } } }

.wiki-extended__view.opened {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 999;
  background-color: rgba(0,0,0, .9);
  .wiki-extended-slider {
    opacity: 0;
    width: 95%;
    height: 95%;
    left: 50%;
    top: 50%;
    animation: sliderIn 1s forwards;
    animation-delay: .5s; }
  .wiki-extended-slider__body {
    height: 100%;
    .wiki-extended-slider__title {
      display: none; }
    .slick-slider,
    .slick-list,
    .slick-track {
      height: 100%; } } }

@keyframes sliderIn {
  0% {
    opacity: 0;
    transform: translate(-50%, -40%); }
  100% {
    opacity: 1;
    transform: translate(-50%, -50%); } }
