.section {
  margin-top: 100px;
  width: 100vw;
  overflow: hidden;
  background-color: $black;
  background-image: url(../img/main-bg-t.png);
  background-size: cover;
  background-position: 50% 50%;
  height: calc(100vh - 100px);
  margin-top: 100px;
  @include resp {
     height: calc(100vh - 70px);
     margin-top: 70px; }
