.calendar {
   height: calc(100vh - 100px);
   margin-top: 100px;
   overflow: auto;
   position: relative;
   @include resp {
      height: calc(100vh - 70px);
      margin-top: 70px; }
   &__top {
      position: absolute;
      left: 0;
      top: 0;
      .directions__title {
         @include resp {
            height: 50px;
            line-height: 50px;
            font-size: 18px; } } }
   &__layout {
      min-height: 400px;
      height: 100%;
      background-image: url('../img/calendar_bg.jpg');
      @include cover;
      @include clr;
      @include rmin(768) {
        padding-top: 40px; }
      @include rmin(1024) {
         padding-top: 50px; }
      @include rmin(1366) {
        padding-top: 60px; }
      @include rmin(1600) {
        padding-top: 80px; } }
   &__row {
      @include clr;
      white-space: nowrap;
      font-size: 0;
      display: flex;
      height: 100%;
      position: relative;
      overflow-x: hidden;

      //+scrollbar
      //   background: #ececec
      //   height: 13px
      //   border-radius: 10px
      //+scrollbar-thumb
      //   border: 2px solid  #ececec
      //   border-radius: 10px
 }      //   background: #9f9f9f
   &__event {
      position: relative;
      display: inline-block;
      font-size: 14px;
      margin-right: 3px;
      min-width: 255px;
      width: 255px;
      height: 100%;
      overflow: hidden;
      z-index: 1;
      &--lg {
         min-width: 513px;
         width: 513px;
         @include resp {
            min-width: 255px;
            width: 255px; } }

      &--variable {
         &:first-child:nth-last-child(1),
         &:first-child:nth-last-child(2),
         &:first-child:nth-last-child(2) ~ div,
         &:first-child:nth-last-child(3),
         &:first-child:nth-last-child(3) ~ div {
            min-width: 513px;
            width: 513px;
            @include resp {
               min-width: 255px;
               width: 255px; } } }
      &-img {
         @include coverdiv;
         background-size: cover;
         background-position: center center;
         background-repeat: no-repeat;
         z-index: 2; }
      &-descr {
         position: absolute;
         top: 0;
         bottom: 0;
         right: 0;
         left: 0;
         padding: 15px 12px;
         background: rgba(#000, .5);
         color: #fff;
         z-index: 3;
         transform: translateY(55%);
         @include trs;
         @include rh(750) {
           transform: translateY(40%); }
         @include rh(680) {
           padding-top: 10px;
           padding-bottom: 10px;
           transform: translateY(40%); }
         @include rmin(768) {
           font-size: 11px; }
         @include rmin(1024) {
           font-size: 16px; } }
      &-text {
        white-space: normal;
        &:first-child {
          @include rmin(768) {
            height: 40px; }
          @include rmin(1024) {
            height: 60px; } }
        & + & {
          color: $accent; } }
      &-more {
         display: block;
         margin: 0 auto;
         color: #fff;
         font-size: 14px;
         width: 160px;
         height: 40px;
         line-height: 40px;
         border: 1px solid $accent;
         text-align: center;
         text-transform: uppercase;
         border-radius: 25px;
         color: $accent;
         background-color: rgba(#000, .2);
         @include rmin(768) {
           margin-top: 15px; }
         @include rmin(1024) {
           margin-top: 30px; }
         &:hover {
           background-color: $accent;
           color: #000; }
         @include r(1024) {
           height: 30px;
           width: 140px;
           line-height: 30px;
           font-size: 14px;
           text-transform: uppercase; } }
      &:hover &-descr {
         transform: translateY(0); } }
   &__chapter {
      max-height: 300px;
      @include clr;
      @include rmin(768) {
        min-height: 119px;
        height: 32%; }
      @include rmin(1024) {
        height: 33.1%;
        min-height: 130px; }
      @include rmin(1600) {
        min-height: 160px;
        height: 33.33%; }
      &:first-child {
         .calendar__items {
            padding-top: 3px; } }
      &:not(:last-child) {
         .calendar__items {
            padding-bottom: 3px; } } }
   &__title {
      float: left;
      margin-right: 3px;
      width: 220px;
      height: 100%;
      @include resp {
         width: 158px; } }
   &__items {
      position: relative;
      width: calc(100% - 223px);
      height: 100%;
      float: left;
      //overflow: hidden
      @include resp {
         width: calc(100% - 164px); } }
   &__datepicker {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      box-shadow: 0 0 9px 0 rgba(0, 0, 0, 0.21);
      text-align: center;
      z-index: 5;
      background-color: #fff;
      @include rmin(768) {
        height: 35px;
        padding-top: 2px;
        padding-bottom: 0px; }
      @include rmin(1024) {
        height: 50px;
        padding-top: 10px;
        padding-bottom: 10px; }
      @include rmin(1366) {
        height: 60px;
        padding: 15px 0 15px 235px; }
      @include rmin(1600) {
        height: 80px; }

      .datepicker {
         display: inline-block; } } }

.directions {
   &--calendar {
      width: 220px;
      display: none;
      @include resp {
         width: 158px; } }
   &__title {
      height: 80px;
      padding: 0 5px;
      font-size: 24px;
      color: #000000;
      text-align: center;
      line-height: 80px;
      box-shadow: 0px 0px 8px 0px rgba(#000, 0.21); }
   &__icon {
     width: 100%;
     height: 80%;
     text-align: center;
     svg {
       width: 80%;
       height: 80%; } }
   &__items {
      display: flex;
      flex-direction: column;
      flex: 1 0 auto; }
   &__link {
      position: relative;
      display: block;
      padding: 10px 0 0 0;
      font-size: 30px;
      text-align: center;
      height: 100%;
      box-shadow: 3px 0px 8px -3px rgba(#000, 0.21);
      @include futura-bold;
      @include tr(background-color .2s);
      &::before {
         content: '';
         @include vcenter;
         @include resp {
            margin-top: 20px;
            transform: translate3d(-50%,-50%,0) scale(0.91); } }
      &--academy {
         background-color: #f6cb02;
         color: #000; }

      &--wiki {
         background-color: #fff;
         color: #9d01b9; }
      &--events {
         background-color: #000;
         color: $accent; }
      .icon-academy-ico,
      .icon-yacht-week-ico,
      .icon-event-ico {
        font-size: 100px;
        width: 1em; } } }

.is-touch {
   .calendar__row {
      overflow-x: auto; } }
