.academy-menu {
    color: #000;
    position: fixed;
    top: 100px;
    width: 100%;
    z-index: 900;
    text-align: center;
    transition: color .2s;
    background-color: #fff;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    @include resp {
      top: 70px;
      height: 60px; }
    @include rmin(1680) {
      top: 100px;
      height: 85px; }

    &__item {
      display: inline-block;
      @include casp-bold;
      text-transform: uppercase;
      transition: border-color .2s;
      border-bottom: 2px solid transparent;
      position: relative;
      @include rmin(1024) {
        border-bottom: 3px solid transparent; }
      &.active {
        border-bottom-color: #000;
        a {
          display: block;
          font-size: 15px;
          @include rmin(1024) {
            font-size: 18px; }
          @include rmin(1600) {
            font-size: 20px; } } }
      & + & {
        margin-left: 9px;
        @include rmin(1024) {
          margin-left: 30px; }
        @include rmin(1600) {
          margin-left: 70px; }
        &:before {
          content: '/';
          position: absolute;
          font-size: 1em;
          font-size: 11px;
          top: 40%;
          transform: translateY(-50%);
          left: -4px;
          @include rmin(1024) {
            left: -15px;
            font-size: 14px; }
          @include rmin(1600) {
            left: -35px;
            font-size: 16px; } } }
      a {
        font-size: 11px;
        padding: 0 2px 5px;
        transition: font-size .5s;
        @include rmin(1024) {
          font-size: 14px; }
        @include rmin(1600) {
          font-size: 16px; } } } }

